import Address from '@/components/Address' //
import EditInfo from '@/components/EditInfo' //
import OpenCoursePayCenter from '@/components/OpenCoursePayCenter' //

Vue.use(Router)

const router = new Router({
  routes: [{
      path: '/Login',
      name: 'Login',
      component: Login
    },
    {
      path: '/Register',
      name: 'Register',
      component: Register
    },
    {
      path: '/VerificationCode',
      name: 'VerificationCode',
      component: VerificationCode
    },
    {
      path: '/',
      name: 'main',
      component: main,
      children: [{
          path: '/index',
          name: 'index',
          component: home
        },
        {
          path: '/SetMeal',
          name: 'SetMeal',
          component: SetMeal
        },
        {
          path: '/PublicClass',
          name: 'PublicClass',
          component: PublicClass
        },
        {
          path: '/Library',
          name: 'Library',
          component: Library
        },
        {
          path: '/ClassQuality',
          name: 'ClassQuality',
          component: ClassQuality
        }
      ]
    },
    // 
    {
      path: '/SetMealDetail/:pid',
      name: 'SetMealDetail',
      component: SetMealDetail
    },
    // 
    {
      path: '/ClassShare/:id',
      name: 'ClassShare',
      component: ClassShare
    },
    {
      path: '/InformationDetail/:id',
      name: 'InformationDetail',
      component: InformationDetail
    },
    {
      path: '/informationList',
      name: 'informationList',
      component: InformationList
    },
    {
      path: '/LibraryDetail/:id',
      name: 'LibraryDetail',
      component: LibraryDetail
    },
    {
      path: '/PayCenter/:type/:id',
      name: 'PayCenter',
      component: PayCenter
    },
    {
      path: '/UserCenter/',
      name: 'UserCenter',
      component: UserCenter
    },
    {
      path: '/Message/',
      name: 'Message',
      component: Message
    },
    {
      path: '/MyClass/',
      name: 'MyClass',
      component: MyClass
    },
    {
      path: '/MyClassDetail/:id',
      name: 'MyClassDetail',
      component: MyClassDetail
    },
    {
      path: '/MyGold/',
      name: 'MyGold',
      component: MyGold
    },
    {
      path: '/MyOrder/',
      name: 'MyOrder',
      component: MyOrder
    },
    {
      path: '/MyCollect/',
      name: 'MyCollect',
      component: MyCollect
    },
    {
      path: '/MyEarnings/',
      name: 'MyEarnings',
      component: MyEarnings
    },
    {
      path: '/MyPublicity/',
      name: 'MyPublicity',
      component: MyPublicity
    },
    {
      path: '/MySet/',
      name: 'MySet',
      component: MySet
    },
    {
      path: '/QualityCourseVideo/',
      name: 'QualityCourseVideo',
      component: QualityCourseVideo
    },
    // 
    {
      path: '/MarketerCenter/',
      name: 'MarketerCenter',
      component: MarketerCenter
    },
    // 
    {
      path: '/TeacherCenter/',
      name: 'TeacherCenter',
      component: TeacherCenter},
    {
      path: '/OpenCoursePayCenter/:id',
      name: 'OpenCoursePayCenter',
      component: OpenCoursePayCenter
    },
  ]
})

