// 
router.beforeEach((to, from, next) => {
  // to: Route:  
  // from: Route: 
  // next: Function:  resolve  next 
  // ABBAscrollTop0
  window.scroll(0, 0);
  // studentNextRoute: 
  const studentNextRoute = ['good-list', 'good-detail', 'cart', 'profile'];
  // teacherNextRoute: 
  const teacherNextRoute = []
  // marketerNextRoute: 
  const marketerNextRoute = []
  let isLogin = global.isLogin; // 
  // nextRoutelogin
  let role = global.$cookies.get("access_role");
  if(role){
    if (role == 'student') {
      if (studentNextRoute.indexOf(to.name) >= 0) {
        if (!isLogin) {
          router.push({
            name: 'login'
          })
        }
      }
    }
    if (role == 'teacher') {
      if (teacherNextRoute.indexOf(to.name) >= 0) {
        if (!isLogin) {
          router.push({
            name: 'login'
          })
        }
      }
    }
    if (role.indexOf('marketer') > -1) {
      if (marketerNextRoute.indexOf(to.name) >= 0) {
        if (!isLogin) {
          router.push({
            name: 'login'
          })
        }
      }
    }

  }
  // loginhome
  if (to.name === 'login') {
    if (isLogin) {
      router.push({
        name: 'home'
      });
    }
  }
  next();
});

export default router;



